:root {
  --background: #0a0a0a;
  --foreground: #e0e0e0;
  --neon-cyan: #00ffff;
  --neon-purple: #8b5cf6;
  --neon-pink: #ec4899;
  --neon-green: #10b981;
  --neon-blue: #3b82f6;
  --neon-orange: #f59e0b;
  --glass-white: rgba(255, 255, 255, 0.08);
  --glass-dark: rgba(0, 0, 0, 0.3);
  --glass-border: rgba(255, 255, 255, 0.15);
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-mono);
  font-weight: 400;
  line-height: 1.6;
  position: relative;
}
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 80%, rgba(0, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(236, 72, 153, 0.05) 0%, transparent 50%);
  animation: backgroundShift 20s ease-in-out infinite;
  z-index: -1;
}
@keyframes backgroundShift {
  0%, 100% {
    background: radial-gradient(circle at 20% 80%, rgba(0, 255, 255, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 80% 20%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 40% 40%, rgba(236, 72, 153, 0.05) 0%, transparent 50%);
  }
  50% {
    background: radial-gradient(circle at 80% 20%, rgba(0, 255, 255, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 20% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 60% 60%, rgba(236, 72, 153, 0.05) 0%, transparent 50%);
  }
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #1a1a1a;
}
::-webkit-scrollbar-thumb {
  background: var(--neon-cyan);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--neon-purple);
}
.glass {
  background: linear-gradient(135deg, var(--glass-white) 0%, rgba(255, 255, 255, 0.05) 100%);
  backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}
.glass::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s;
}
.glass:hover::before {
  left: 100%;
}
.neon-glow {
  box-shadow: 0 0 20px var(--neon-cyan),
    0 0 40px rgba(0, 255, 255, 0.3),
    inset 0 0 20px rgba(0, 255, 255, 0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}
.neon-glow:hover {
  box-shadow: 0 0 30px var(--neon-cyan),
    0 0 60px rgba(0, 255, 255, 0.5),
    0 0 90px rgba(0, 255, 255, 0.3),
    inset 0 0 30px rgba(0, 255, 255, 0.2);
  transform: translateY(-2px);
}
.neon-glow-purple {
  box-shadow: 0 0 20px var(--neon-purple),
    0 0 40px rgba(139, 92, 246, 0.3),
    inset 0 0 20px rgba(139, 92, 246, 0.1);
}
.neon-glow-purple:hover {
  box-shadow: 0 0 30px var(--neon-purple),
    0 0 60px rgba(139, 92, 246, 0.5),
    0 0 90px rgba(139, 92, 246, 0.3),
    inset 0 0 30px rgba(139, 92, 246, 0.2);
}
.neon-glow-pink {
  box-shadow: 0 0 20px var(--neon-pink),
    0 0 40px rgba(236, 72, 153, 0.3),
    inset 0 0 20px rgba(236, 72, 153, 0.1);
}
.neon-glow-pink:hover {
  box-shadow: 0 0 30px var(--neon-pink),
    0 0 60px rgba(236, 72, 153, 0.5),
    0 0 90px rgba(236, 72, 153, 0.3),
    inset 0 0 30px rgba(236, 72, 153, 0.2);
}
.typewriter {
  overflow: hidden;
  border-right: 2px solid var(--neon-cyan);
  white-space: nowrap;
  animation: typewriter 3s steps(40) 1s 1 normal both, blink 1s infinite;
}
@keyframes typewriter {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes blink {
  0%, 50% {
    border-color: var(--neon-cyan);
  }
  51%, 100% {
    border-color: transparent;
  }
}
